    /**
     * @description: 表单中扩展字段处理
     * @return: void
     * @author: ZJM
     */
    toExtendRulesAndShow(row) {
      // 保存扩展字段数据
      var arr = row.extendPropertyValueVoList.map((item) => {
        item.relationshipTypeId = this.$store.state.deviceManangeId;
        return item;
      });
      this.extendPropertyValueVoList = arr;
      this.rules = JSON.parse(JSON.stringify(this.OldRules));
      for (var item in this.extendPropertyValueVoList) {
        // 若类型是日期
        if (this.extendPropertyValueVoList[item].propertyType === 3) {
          this.rules[this.extendPropertyValueVoList[item].propertyCode] = [
            { validator: this.setValidateForm, trigger: ['blur', 'change'] }
          ];
        } else { // 若类型是字符或数字
          this.rules[this.extendPropertyValueVoList[item].propertyCode] = [
            { validator: this.setValidateForm, trigger: 'blur' }
          ];
        }
      }
    },
    /**
     * @description: 保存扩展字段数据
     * @return: void
     * @author: ZJM
     */
    async saveExtendValue() {
      var arr = this.extendPropertyValueVoList.map((item) => {
        if (item.extendValue === null) {
          item.extendValue = '';
        }
        return item;
      });

      try {
        const res = await extendPropertyValueInsertOrUpdateBatchAction({}, arr);
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          // this.findTableList();
          this.getDeviceInfo();
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
