<template>
  <el-container class="content">
    <el-main>
      <div class="area-main-conent">
        <HeaderTitle
          class="header-title"
          :titleName="CONSTANTS_DEVICE_CONFIGURATION.COLLECT_CONFIG"
        ></HeaderTitle>
        <el-form
          ref="deviceInfo"
          size="small"
          :model="formInline"
          :rules="formInlineRules"
          class="demo-form-inline"
          :show-message="true"
        >
          <el-row>
            <el-col :span="24">
              <table cellspacing="0" cellpadding="0" class="formTable">
                <tr>
                  <td>
                    <span class="errorStar">*</span>
                    {{CONSTANTS_DEVICE_CONFIGURATION.IS_COLLECT}}
                  </td>
                  <td>
                    <el-form-item prop="isCollect">
                      <el-radio-group v-model="formInline.isCollect">
                        <el-radio :label="1">{{VARIABLE.YES}}</el-radio>
                        <el-radio :label="0">{{VARIABLE.NO}}</el-radio>
                      </el-radio-group>
                    </el-form-item>
                  </td>
                </tr>
                <tr>
                  <td>
                    <span class="errorStar">*</span>
                    {{CONSTANTS_DEVICE_CONFIGURATION.IS_PRODUCTION_COUNT}}
                  </td>
                  <td>
                    <el-form-item prop="isProductionCount">
                      <el-radio-group v-model="formInline.isProductionCount">
                        <el-radio :label="1">{{VARIABLE.YES}}</el-radio>
                        <el-radio :label="0">{{VARIABLE.NO}}</el-radio>
                      </el-radio-group>
                    </el-form-item>
                  </td>
                </tr>
                <tr>
                  <td>
                    <span v-if="formInline.isCollect" class="errorStar">*</span>
                    {{CONSTANTS_DEVICE_CONFIGURATION.COLLECT_TYPE}}
                  </td>
                  <td>
                    <el-form-item prop="collectTypeId">
                      <el-cascader
                        v-model="formInline.collectTypeId"
                        :options="typeOptions"
                        :show-all-levels="false"
                        clearable
                        class="formItem_full"
                      ></el-cascader>
                    </el-form-item>
                  </td>
                </tr>
                <tr>
                  <td>
                    <!-- <span class="errorStar">*</span> -->
                    {{CONSTANTS_DEVICE_CONFIGURATION.IP}}
                  </td>
                  <td>
                    <el-form-item prop="ipAddress">
                      <el-input
                        :class="['ipClass',{'ipDot':index<3}]"
                        ref="ipInput"
                        v-for="(item,index) in formInline.ipAddress"
                        :key="index"
                        v-model="item.value"
                        @input="checkIpVal(item)"
                        @keyup.native="turnIpPosition(item,index,$event)"
                      ></el-input>
                    </el-form-item>
                  </td>
                </tr>
                <tr>
                  <td>
                   <!--  <span class="errorStar">*</span> -->
                    {{CONSTANTS_DEVICE_CONFIGURATION.PORT}}
                  </td>
                  <td>
                    <el-form-item prop="port">
                      <el-input
                        onkeyup="value=value.replace(/[^\d]/g,'')" maxlength="6" v-model="formInline.port"></el-input>
                    </el-form-item>
                  </td>
                </tr>
                <tr>
                  <td>
                    <!-- <span class="errorStar">*</span> -->
                    <span>{{CONSTANTS_DEVICE_CONFIGURATION.NET_SERIAL_NUMBER}}</span>
                  </td>
                  <td>
                    <el-form-item prop="netSerialNumber">
                      <el-input v-model="formInline.netSerialNumber"></el-input>
                    </el-form-item>
                  </td>
                </tr>
              </table>
            </el-col>
           <!--  <el-col :span="24">
              <el-col :span="24" class="colBorderNone">
                <el-button type="primary" @click="saveDeviceInfo">{{BTN_TEXT.SAVE}}</el-button>
                <el-button @click="reset">{{BTN_TEXT.RESET}}</el-button>
              </el-col>
            </el-col> -->
          </el-row>
        </el-form>
      </div>
    </el-main>
  </el-container>
</template>

<script>
import js from '@/assets/js/company/DeviceManange/deviceInfoCollect';
export default js;
</script>

<style lang="less" scoped>
@import '../../../assets/less/company/DeviceManange/deviceInfoCollect.less';
</style>
