 * @LastEditTime: 2022-09-07 17:40:27
 -->
<template>
  <el-container class="content">
    <el-main>
        <div class="area-main-conent">
          <el-header>
            <HeaderTitle class="header-title" :titleName="CONSTANTS_DEVICE_CONFIGURATION.CAN_CHOOSE_PARAMS">
              <div slot="head-operation" class="head-operation">
                <HeadOperationItem
                  name="addParams"
                  @onOprationItem="insertParam"
                >
                  <i slot="operation-icon" class="icon-add"></i>
                  <span slot="operation-name">{{BTN_TEXT.ADD}}</span>
                </HeadOperationItem>
                <HeadOperationItem
                  name="addParams"
                  @onOprationItem="synaParam"
                >
                  <i slot="operation-icon" class="icon-add"></i>
                  <span slot="operation-name">{{PLC_PARAMS.SYNC_PARAMS}}</span>
                </HeadOperationItem>
              </div>
            </HeaderTitle>
          </el-header>
          <div class="area-main-conent">
            <vxe-grid
              border="full"
              resizable
              keep-source
              ref="canSelect"
              class="vxe-table-element"
              height="300"
              :columns="tableColumnsCanSelect"
              :data="tableDatasCanSelect"
              :edit-config="{trigger: 'click', mode: 'cell', showStatus: true, icon: 'fa fa-file-text-o'}"
              @edit-actived="editActivedCanSelectEvent"
              @edit-closed="editClosedCanSelectEvent"
            ></vxe-grid>
          </div>
        </div>
        <div class="area-main-conent">
          <el-header>
            <HeaderTitle class="header-title" :titleName="CONSTANTS_DEVICE_CONFIGURATION.DEFINED_PARAMS">
              <div slot="head-operation" class="head-operation">
              <HeadOperationItem
                name="addParams"
                @onOprationItem="insertEvent"
              >
                <i slot="operation-icon" class="icon-add"></i>
                <span slot="operation-name">{{BTN_TEXT.ADD}}</span>
              </HeadOperationItem>
            </div>
            </HeaderTitle>
          </el-header>
          <div class="area-main-conent">
            <vxe-grid
              border="full"
              resizable
              keep-source
              ref="defined"
              class="vxe-table-element"
              height="300"
              :edit-rules="validRulesDefined"
              :columns="tableColumnsDefined"
              :data="tableDatasDefined"
              :edit-config="{trigger: 'click', mode: 'cell', showStatus: true, icon: 'fa fa-file-text-o'}"
              @edit-actived="editActivedDefinedEvent"
            ></vxe-grid>
          </div>
        </div>
        <div class="area-main-conent">
          <el-header>
            <HeaderTitle class="header-title" :titleName="CONSTANTS_DEVICE_CONFIGURATION.WARN_PARAMS"></HeaderTitle>
          </el-header>
          <div class="area-main-conent">
            <vxe-grid
              border="full"
              resizable
              keep-source
              ref="warning"
              class="vxe-table-element"
              height="300"
              :columns="tableColumnsWarning"
              :data="tableDatasWarning"
              :edit-config="{trigger: 'click', mode: 'cell', showStatus: true, icon: 'fa fa-file-text-o'}"
            ></vxe-grid>
          </div>
        </div>
        <!-- <div class="area-main-conent">
          <el-button type="primary" @click="fullValidEvent" >{{BTN_TEXT.SAVE}}</el-button>
          <el-button  @click="reset">{{BTN_TEXT.RESET}}</el-button>
        </div> -->

      <el-dialog :class="[$store.state.dialogMarginClass]" :title="'同步参数'" :width="'80%'" :visible.sync="dialogVisibleQrCode">
        <addChildrenParams
        :fatherNodeData="fatherNodeData"
        :destoryData="destoryArr"
        :typeId="ztreeSelectedNode.id"
        :typeName="ztreeSelectedNode.name"
        @goBack="goBack"
        ></addChildrenParams>
      </el-dialog>

      <el-dialog
          :class="[$store.state.dialogMarginClass]"
          :title="PLC_PARAMS.SYNC_PARAMS"
          :width="'500px'"
          :visible.sync="dialogVisibleParams"
        >
          <el-form ref="paramsForm" :model="formParams" :rules="rulesParams">
            <el-form-item
              :label="PLC_PARAMS.MODEL"
              prop="model"
              :label-width="formLabelWidthShort"
            >
              <el-select
                v-model="formParams.model"
                :placeholder="MSG.PLEASE_CHOOSE"
                filterable
              >
                <el-option
                  v-for="item in modelOptions"
                  :key="item.dicTemplateId"
                  :label="item.enName"
                  :value="item.dicTemplateId"
                ></el-option>
              </el-select>
            </el-form-item>
          </el-form>
          <span slot="footer" class="dialog-footer">
            <el-button @click="submitParams">{{ BTN_TEXT.CONFIRM }}</el-button>
            <el-button type="primary" @click="dialogVisibleParams = false">{{
              BTN_TEXT.CANCEL
            }}</el-button>
          </span>
        </el-dialog>

    </el-main>
  </el-container>
</template>

<script>
import js from '@/assets/js/company/DeviceManange/deviceInfoConfig';
export default js;
</script>

<style lang="less" scoped>
@import '../../../assets/less/company/DeviceManange/deviceInfoConfig.less';
</style>
