/*
 * @Descripttion: 设备详情扩展字段组件
 * @version: 0.1
 * @Author: ZJM
 * @Date: 2020-05-22 14:52:15
 * @LastEditors: ZJM
 * @LastEditTime: 2021-03-02 17:40:17
 */
import { showSuccessMessage, showWarningMessage } from '@/utils/message';
import { showErrorNotify } from '@/utils/notify';
// 导入所需接口
import {
  extendPropertyInsertOrUpdateBatchAction,
  extendPropertyDeleteByIdsAction,
  extendPropertyGetAllAction
} from '@/api/management';
import { STATUS_CODE, TITLE, MSG, VARIABLE, BTN_TEXT, CONSTANTS_DEVICE_CONFIGURATION } from '@/utils/constantsFile';
// 引入标题组件
import HeaderTitle from '@/components/common/PageNavBar/HeaderTitle';
// 引入标题元素组件
import HeadOperationItem from '@/components/common/PageNavBar/HeadOperationItem';
// 导入所需接口
export default {
  components: {
    // 加载标题组件
    HeaderTitle,
    HeadOperationItem
  },
  props: {
  },
  data() {
    return {
      BTN_TEXT: BTN_TEXT,
      CONSTANTS_DEVICE_CONFIGURATION: CONSTANTS_DEVICE_CONFIGURATION,
      titleName: '',
      oldRow: {},
      // 自定义参数验证
      validRulesDefined: {
        extendName: [{ required: true, message: MSG.PLEASE_ENTER_CONTENT }]
      },
      // 自定义参数表头配置
      tableColumnsDefined: [
        {
          type: 'seq',
          field: '',
          title: VARIABLE.SEQ,
          width: '80',
          align: 'center'
        },
        {
          field: 'extendName',
          title: CONSTANTS_DEVICE_CONFIGURATION.EXTEND_NAME,
          align: 'center',
          editRender: { name: 'input' }
        },
        {
          field: 'remark',
          title: CONSTANTS_DEVICE_CONFIGURATION.ANNOTATION,
          align: 'center',
          editRender: { name: 'input' }
        },
        {
          field: '',
          title: BTN_TEXT.OPERATION,
          width: '100',
          align: 'center',
          slots: {
            default: ({ row, column }) => {
              return [
                <i
                  onClick={() => this.deleteRow(row, column)}
                  class="icon-table-delete"
                ></i>
              ];
            }
          }
        }
      ],
      // 自定义参数数据
      tableDatasDefined: []
    };
  },
  computed: {},
  created() {

  },
  mounted() {
    // 获取自定义参数数据
    this.getDefinedData();
  },
  methods: {
    /**
     * @description: 获取自定义参数数据
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    async getDefinedData() {
      try {
        const res = await extendPropertyGetAllAction();
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.tableDatasDefined = res.rows;
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description: 执行新增一行
     * @param row 行数据
     * @return: 无
     * @author: ZJM
     */
    async insertEvent() {
      const record = {
        extendType: 1
      };
      // 往表格插入临时数据，从指定位置插入一行或多行
      await this.$refs.defined.insertAt(record, -1);
    },
    deleteRow(row) {
      this.$XModal.confirm(MSG.CONFIRM_DELETE).then(type => {
        if (type === 'confirm') {
          this.$refs.defined.remove(row);
        }
      });
    },
    /**
     * @description: 执行验证
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    async fullValidEvent() {
      const errMap = await this.$refs.defined
        .fullValidate()
        .catch(errMap => errMap);
      if (errMap) {
        var msgList = [];
        Object.values(errMap).forEach(errList => {
          errList.forEach((params, index) => {
            const { column, rules } = params;
            rules.forEach(rule => {
              msgList.push(
                `${VARIABLE.THE} ${index + 1} ${VARIABLE.ROW} ${column.title} ${MSG.VALIDATION_ERROR}：${rule.message}`
              );
            });
          });
        });
        this.$XModal.alert({
          status: 'error',
          message: () => {
            return [
              <div class="red" style="max-height: 400px;overflow: auto;">
                {msgList.map(msg => (
                  <div>{msg}</div>
                ))}
              </div>
            ];
          }
        });
      } else {
        // 通过验证，执行提交
        this.submitDefined();
      }
    },
    /**
     * @description: 执行保存，进行调用方法分离
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    submitDefined() {
      const {
        insertRecords, removeRecords, updateRecords
      } = this.$refs.defined.getRecordset();
      // 若存在新增数据
      if (insertRecords.length > 0) {
        this.submitDefinedInsert(insertRecords);
      }
      // 若存在删除数据
      if (removeRecords.length > 0) {
        var newremoveArr = removeRecords.map(val => {
          return val.extendId;
        });
        this.submitDefinedRemove(newremoveArr);
      }
      // 若存在修改数据
      if (updateRecords.length > 0) {
        var newupdateArr = updateRecords.map(val => {
          const json = {};
          json.extendId = val.extendId;
          json.extendName = val.extendName;
          json.remark = val.remark;
          return json;
        });
        this.submitDefinedUpdate(newupdateArr);
      }
    },
    /**
     * @description: 执行新增
     * @param rows 需要新增的数据
     * @return: 无
     * @author: ZJM
     */
    async submitDefinedInsert(rows) {
      try {
        const res = await extendPropertyInsertOrUpdateBatchAction(
          {},
          rows);
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          // 提示正常信息
          showSuccessMessage(res.message);
          this.getDefinedData();
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description: 执行修改
     * @param rows 需要修改的数据
     * @return: 无
     * @author: ZJM
     */
    async submitDefinedUpdate(rows) {
      try {
        const res = await extendPropertyInsertOrUpdateBatchAction(
          {},
          rows);
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          // 提示正常信息
          showSuccessMessage(res.message);
          this.getDefinedData();
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description: 执行删除
     * @param rows 需要删除的数据
     * @return: 无
     * @author: ZJM
     */
    async submitDefinedRemove(rows) {
      try {
        const res = await extendPropertyDeleteByIdsAction(
          {
            ids: rows
          });
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          // 提示正常信息
          showSuccessMessage(res.message);
          this.getDefinedData();
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description: 返回详情主页
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    goBack() {
      this.$emit('toggleExtend', false);
    }
  }
};
