<template>
  <el-container class="content">
    <el-header>
      <HeaderTitle :titleName="CONSTANTS_DEVICE_CONFIGURATION.EXTEND_MAINTAIN">
         <div slot="head-operation" class="head-operation">
            <el-button
            type="primary"
            size="mini"
            @click="goBack"
          >{{BTN_TEXT.RETURN}}</el-button>
        </div>
      </HeaderTitle>
    </el-header>
    <el-main>
      <div class="area-main-conent">
        <el-header>
          <HeaderTitle :titleName="CONSTANTS_DEVICE_CONFIGURATION.EXTEND">
            <div slot="head-operation" class="head-operation">
              <el-button
                type="default"
                size="mini"
                @click="insertEvent"
              >{{BTN_TEXT.ADD}}</el-button>
            </div>
          </HeaderTitle>
        </el-header>
        <vxe-grid
          border="full"
          resizable
          keep-source
          ref="defined"
          class="vxe-table-element"
          height="300"
          :edit-rules="validRulesDefined"
          :columns="tableColumnsDefined"
          :data="tableDatasDefined"
          :edit-config="{trigger: 'click', mode: 'row', showStatus: true, icon: 'fa fa-file-text-o'}"
        ></vxe-grid>
        <div class="area-main-conent">
          <el-button type="primary" @click="fullValidEvent">{{BTN_TEXT.SAVE}}</el-button>
        </div>
      </div>
    </el-main>
  </el-container>
</template>

<script>
import js from '@/assets/js/main/DeviceConfiguration/deviceInfoExtend';
export default js;
</script>

<style lang="less" scoped>
@import '../../../assets/less/main/DeviceConfiguration/deviceInfoExtend.less';
</style>
