// 引入标题组件
import HeaderTitle from '@/components/common/PageNavBar/HeaderTitle';
import { showSuccessMessage, showWarningMessage } from '@/utils/message';
import { showErrorNotify } from '@/utils/notify';
import { STATUS_CODE, TITLE, VARIABLE, BTN_TEXT, PLC_PARAMS } from '@/utils/constantsFile';
import XEUtils from 'xe-utils'; // 注： 在别的组件里调用xe-utils要用this.$utils
// 导入所需接口
import {
  deviceTypeParameterRelationshipSelectByDeviceTypeIdAction,
  deviceAdminDeviceParameterRelationshipDeviceParameterRelationshipInsertBatch,
  deviceTypeParameterRelationshipDeleteAction
} from '@/api/management';
export default {
  components: {
    // 加载标题组件
    HeaderTitle
  },
  props: {
    // 模板名称
    typeName: {
      type: String,
      default: ''
    },
    // 模板id
    typeId: {
      type: [Number, String]
    },
    // 类型所属参数
    fatherNodeData: {
      type: Array,
      default: () => {
        return [];
      }
    },
    // 类型所属参数
    destoryData: {
      type: Array,
      default: () => {
        return [];
      }
    }
  },
  data() {
    return {
      BTN_TEXT: BTN_TEXT,
      // 模板所属参数
      childrenNodeData: [],
      // 页面标题
      titleName: '选择参数',
      // table分页配置
      tablePage: {
        total: 0,
        currentPage: 1,
        pageSize: 20,
        align: 'left',
        pageSizes: [5, 20, 50, 100, 200, 500],
        layouts: [
          'Sizes',
          'PrevJump',
          'PrevPage',
          'Number',
          'NextPage',
          'NextJump',
          'FullJump',
          'Total'
        ],
        perfect: true,
        slots: {
          // 使用自定义插槽
          left: 'pager_left'
        }
      },
      // table数据
      tableDatas: [],
      showList: [],
      // table表头配置
      tableColumns: [
        {
          type: 'checkbox',
          align: 'center'
        },
        {
          field: 'cnName',
          title: PLC_PARAMS.ZH_NAME,
          align: 'center'
        },
        {
          field: 'parameterName',
          title: PLC_PARAMS.PARAMETER_NAME,
          align: 'center'
        },
        {
          field: 'enName',
          title: PLC_PARAMS.EN_NAME,
          align: 'center'
        },
        {
          field: 'shortName',
          title: PLC_PARAMS.SHORT_NAME,
          align: 'center'
        },
        {
          field: 'unit',
          title: PLC_PARAMS.UNIT,
          align: 'center'
        },
        {
          field: 'isWarning',
          title: PLC_PARAMS.IS_WARNING,
          align: 'center',
          formatter: ({ cellValue, row, column }) => {
            if (row.isWarning === 1) {
              return VARIABLE.YES;
            } else if (row.isWarning === 0) {
              return VARIABLE.NO;
            }
          }
        },
        {
          field: 'valueType',
          title: PLC_PARAMS.VALUE_TYPE,
          align: 'center'
        },
        {
          field: 'description',
          title: PLC_PARAMS.DESCRIPTION,
          align: 'center'
        },
        {
          field: 'remark',
          title: PLC_PARAMS.REMARK,
          align: 'center'
        }
      ],
      filterName: ''
    };
  },
  computed: {},
  created() {
    // 数据写入table
    this.tableDatas = JSON.parse(JSON.stringify(this.fatherNodeData));
    this.showList = this.fatherNodeData;
  },
  mounted() {
    // this.getChildrenNodeData();
  },
  methods: {
    checCheckboxkAble: function({ row }) {
      // console.log(row);
      // console.log(this.destoryData, row.dictionaryId);
      if (this.destoryData.indexOf(row.dictionaryId) > -1) {
        return false;
      } else {
        return true;
      }
    },
    /**
     * @description:查询模板所绑定的数据并比对
     * @param 无
     * @return:无
     * @author: ZJM
     */
    async getChildrenNodeData() {
      try {
        const res = await deviceTypeParameterRelationshipSelectByDeviceTypeIdAction({
          // deviceTypeId: this.typeId,
          deviceTypeId: 2,
          current: VARIABLE.ONE_NUMBER,
          size: VARIABLE.BIG_NUMBER
        }, {});
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          // 获取数据
          this.childrenNodeData = res.rows;
          // 判断子类型是否新增过参数
          if (this.childrenNodeData.length !== 0) {
            // 通过遍历对重复的参数进行勾选
            for (var i in this.childrenNodeData) {
              for (var j in this.fatherNodeData) {
                if (
                  this.childrenNodeData[i].dictionaryId ===
                  this.fatherNodeData[j].dictionaryId
                ) {
                  this.tableDatas[j].checked = 1;
                  this.$refs.xGrid.toggleCheckboxRow(this.fatherNodeData[j]);
                }
              }
            }
          }
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description:提交数据
     * @param 无
     * @return:无
     * @author: ZJM
     */
    async submit() {
      // 若模板新增过数据，则先清空原绑定数据
      if (this.childrenNodeData.length > 0) {
        try {
          const res = await deviceTypeParameterRelationshipDeleteAction({}, {
            conditionJson1: [
              {
                column: 'deviceTypeId',
                operator: VARIABLE.ONE_NUMBER,
                ptype: VARIABLE.PARAM_INT,
                type: VARIABLE.PARAM_EQ,
                value: this.typeId
              }
            ]
          });
          if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
            // const data = this.$refs.xGrid.getCheckboxRecords();
            // if (data.length > 0) {
            // 清空后执行保存
            this.save();
            // } else {
            // 返回主页面
            // this.goBack();
            // 提示正常信息
            // showSuccessMessage(res.message);
            // }
          } else {
            // 提示异常信息
            showWarningMessage(res.message);
          }
        } catch (error) {
          showErrorNotify(TITLE.NOTIFY, error);
        } finally {
        }
      } else {
        this.save();
      }
    },
    /**
     * @description:保存数据
     * @param 无
     * @return:无
     * @author: ZJM
     */
    async save() {
      try {
        // 获取选中的数据
        const data = this.$refs.xGrid.getCheckboxRecords();
        const arr = [];
        // 通过遍历生成新的数据
        for (var i in data) {
          arr.push(data[i].dictionaryId);
        }
        if (arr.length !== 0) {
          const res = await deviceAdminDeviceParameterRelationshipDeviceParameterRelationshipInsertBatch({
            deviceCode: this.$store.state.deviceManangeCode
          }, arr);
          if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
            this.tableDatas = [];
            // 返回主页面
            this.goBack(1);
            // 提示正常信息
            showSuccessMessage(res.message);
          } else {
            // 提示异常信息
            showWarningMessage(res.message);
          }
        } else {
          // 返回主页面
          this.goBack(1);
          // 提示正常信息
          showSuccessMessage('操作成功');
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    searchAllTable() {
      const filterName = XEUtils.toString(this.filterName).trim().toLowerCase();
      if (filterName) {
        // const filterRE = new RegExp(filterName, 'gi');
        const searchProps = ['cnName', 'parameterName', 'enName', 'shortName'];
        const rest = this.tableDatas.filter(item => searchProps.some(key => XEUtils.toString(item[key]).toLowerCase().indexOf(filterName) > -1));
        var arr = rest.map(row => {
          const item = Object.assign({}, row);
          searchProps.forEach(key => {
            // item[key] = XEUtils.toString(item[key]).replace(filterRE, match => `<span class="keyword-lighten">${match}</span>`);
          });
          return item;
        });
        this.showList = arr;
      } else {
        this.showList = this.fatherNodeData;
      }
      for (var i in this.showList) {
        for (var j in this.tableDatas) {
          if (
            this.showList[i].dictionaryId ===
            this.tableDatas[j].dictionaryId && this.tableDatas[j].checked === 1
          ) {
            // this.tableDatas[j].checked = 1;
            this.$refs.xGrid.toggleCheckboxRow(this.showList[i]);
          }
        }
      }
      // return this.tableDatas;
    },
    selectAllEvent({ checked, records }) {
      if (checked) {
        for (var i = 0; i < this.tableDatas.length; i++) {
          this.tableDatas[i].checked = 1;
        }
      } else {
        for (var j = 0; j < this.tableDatas.length; j++) {
          this.tableDatas[j].checked = 0;
        }
      }
    },
    selectChangeEvent({ checked, records, rowIndex, row }) {
      if (checked) {
        for (var i = 0; i < this.tableDatas.length; i++) {
          if (this.tableDatas[i].dictionaryId === row.dictionaryId) {
            this.tableDatas[i].checked = 1;
          }
        }
      } else {
        for (var j = 0; j < this.tableDatas.length; j++) {
          if (this.tableDatas[j].dictionaryId === row.dictionaryId) {
            this.tableDatas[j].checked = 0;
          }
        }
      }
    },
    /**
     * @description: 返回按钮点击
     * @param {}
     * @return:
     * @author: YZZ
     */
    goBack(value) {
      this.$emit('goBack', value);
    }
  }
};
