    /**
     * @description: 清除图片
     * @param row 行数据
     * @param column 表头
     * @return: 无
     * @author: ZJM
     */
    clearImage(row, column) {
      this.$XModal.confirm(CONSTANTS_DEVICE_CONFIGURATION.CONFIRM_IMAGE).then(type => {
        if (type === 'confirm') {
          delete row.imageUrl;
          delete row.imageFileId;
          this.$refs.xGrid.reloadRow(row, row, column);
        }
      });
    },
    /**
     * @description: 点击选择图片
     * @param row 行数据
     * @param column 表头
     * @author: ZJM
     */
    showUpload(row, column) {
      this.$refs.xGrid.readFile({
        multiple: false,
        types: ['jpg', 'png', 'jpeg']
      }).then(evnt => {
        this.uploadSectionIMG(evnt.target.files[0], row, column);
      });
    },
    /**
     * @description: 上传图片
     * @param flie 文件数据
     * @param row 行数据
     * @param column 表头
     * @return: 无
     * @author: ZJM
     */
    async uploadSectionIMG(file, row, column) {
      const fd = new FormData();
      fd.append('file', file);
      fd.append('fileResourceTypeEnum', 'DEVICE_IMAGE');
      fd.append('relationshipId', 0);
      try {
        const res = await fileResourceUploadOneFileAction({}, fd);
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          row.imageUrl = this.$store.state.publicUrl + res.rows.fileUrl;
          row.imageFileId = res.rows.resourceId;
          this.$refs.xGrid.reloadRow(row, row, column);
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
