    /**
     * @description: 联级器选择显示值
     * @param value 选中值
     * @param list 列表值
     * @return: 显示值
     * @author: ZJM
     */
    getCascaderLabel(value, list) {
      const values = value || [];
      const labels = [];
      const matchCascaderData = (index, list) => {
        const val = values[index];
        if (list && values.length > index) {
          list.forEach(item => {
            if (item.value === val) {
              if (value.length > 0) {
                if (item.value === value[value.length - 1]) {
                  labels.push(item.label);
                }
              }
              matchCascaderData(++index, item.children);
            }
          });
        }
      };
      matchCascaderData(0, list);
      return labels.join('');
    },
    /**
     * @description: 选择日期显示值
     * @param value 选择值
     * @param format 日期格式化
     * @return: 显示值
     * @author: ZJM
     */
    formatDate(value, format) {
      if (!value) {
        return '';
      } else {
        return moment(value).format(format);
      }
    },
    /**
     * @description: 选择下拉显示值
     * @param value 选中值
     * @param list 列表值
     * @return:显示值
     * @author: ZJM
     */
    getSelectLabel(value, list, valueProp = 'value', labelField = 'label') {
      const item = this.$utils.find(list, item => item[valueProp] === value);
      return item ? item[labelField] : null;
    },
