<!--
 * @Descripttion: 设备基础信息页面
 * @version:
 * @Author: ZJM
 * @Date: 2020-04-14 10:09:28
 * @LastEditors: ZJM
 * @LastEditTime: 2022-05-11 14:40:32
 -->
<template>
  <el-container class="content">
    <el-main>
      <div class="area-main-conent">
        <el-form
          ref="deviceInfo"
          :model="formInline"
          :rules="rules"
          class="demo-form-inline"
          :label-width="formLabelWidth"
          :show-message="true"
        >
          <el-row>
            <el-col :span="20">

                 <el-col :span="12">
                  <el-form-item prop="deviceName" :label="CONSTANTS_DEVICE_CONFIGURATION.DEVICE_NAME">
                    <el-input v-model="formInline.deviceName"></el-input>
                  </el-form-item>
                </el-col>

                 <el-col :span="12">
                  <el-form-item  :label="CONSTANTS_DEVICE_CONFIGURATION.DEVICE_CODE">
                    <el-input readonly="readonly" v-model="formInline.deviceCode"></el-input>
                  </el-form-item>
                </el-col>

              <el-col :span="12">
                <el-form-item
                  prop="commissioningDate"
                  :label="CONSTANTS_DEVICE_CONFIGURATION.COMMISSIONING_DATE"
                >
                  <el-date-picker
                    type="date"
                    v-model="formInline.commissioningDate"
                    format="yyyy-MM-dd"
                    value-format="yyyy-MM-dd"
                    class="formItem_full"
                  ></el-date-picker>
                </el-form-item>
              </el-col>
              <el-col :span="12">
                <el-form-item prop="prCode" :label="CONSTANTS_DEVICE_CONFIGURATION.DEVICE_CODE">
                  <el-input v-model="formInline.devicePriCode"></el-input>
                  <!-- <el-input v-model="formInline.deviceName"></el-input> -->
                </el-form-item>
              </el-col>

              <el-col :span="12">
                  <el-form-item
                    prop="deviceWarningAuto"
                    :label="CONSTANTS_DEVICE_CONFIGURATION.WARN_AUTO_SHORT"
                  >
                    <el-select v-model="formInline.deviceWarningAuto" class="formItem_full">
                      <el-option
                        v-for="item in deviceWarningAutoOptions"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value"
                      ></el-option>
                    </el-select>
                  </el-form-item>
              </el-col>

              <el-col :span="12">
                  <el-form-item
                    prop="isProductionDevice"
                    :label="'是否产能设备'"
                  >
                    <el-select v-model="formInline.isProductionDevice" class="formItem_full">
                      <el-option
                        v-for="item in deviceWarningAutoOptions"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value"
                      ></el-option>
                    </el-select>
                  </el-form-item>
              </el-col>

              <el-col :span="12">
                <el-form-item :label="CONSTANTS_DEVICE_CONFIGURATION.MODE"   prop="mode">
                  <el-input v-model="formInline.mode"></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="12">
                <el-form-item
                  prop="repairTimeValue"
                  :label="CONSTANTS_DEVICE_CONFIGURATION.REPAIR_TIME"
                >
                  <el-input
                    type="number"
                    min="0"
                    max="999999"
                    v-model="formInline.repairTimeValue"
                    class="formItem_time"
                  ></el-input>
                  <el-select v-model="formInline.repairTimeUnit" class="formItem_unit">
                    <el-option
                      v-for="item in repairTimeUnitOptions"
                      :key="item.value"
                      :label="item.label"
                      :value="item.value"
                    ></el-option>
                  </el-select>
                </el-form-item>
              </el-col>
              <el-tooltip class="item" effect="light" content="未配置无法计算时间稼动率" placement="top">
                <el-col :span="12">
                  <el-form-item
                    prop="dayWorkTime"
                    :label="CONSTANTS_DEVICE_CONFIGURATION.DAY_WORK_TIME"
                  >
                    <el-input  type="number"
                      min="0"
                      max="24" v-model="formInline.dayWorkTime"></el-input>
                  </el-form-item>
                </el-col>
              </el-tooltip>
              <el-col :span="12">
                <el-form-item :label="CONSTANTS_DEVICE_CONFIGURATION.LOCATION" prop="locationX" >
                  <el-input v-model="formInline.locationX"></el-input>
                </el-form-item>
              </el-col>
              <el-tooltip class="item" effect="light" content="未配置导致设备生产周期不可控" placement="top">
                <el-col :span="12">
                  <el-form-item
                    prop="maxProductionCycleSecond"
                    :label="'最大生产周期（秒）'"
                  >
                    <el-input  type="number"
                      min="0"
                      max="999999" v-model="formInline.maxProductionCycleSecond"></el-input>
                  </el-form-item>
                </el-col>
              </el-tooltip>
              <el-tooltip class="item" effect="light" content="未配置无法计算速度稼动率" placement="top">
                <el-col :span="12">
                  <el-form-item
                    prop="productionCycleSecond"
                    :label="'平均生产周期（秒）'"
                  >
                    <el-input  type="number"
                      min="0"
                      max="999999" v-model="formInline.productionCycleSecond"></el-input>
                  </el-form-item>
                </el-col>
              </el-tooltip>
              <el-col :span="12">
                <el-form-item
                  prop="dayProduction"
                  :label="CONSTANTS_DEVICE_CONFIGURATION.DAY_PRODUCTION"
                >
                  <el-input  type="number"
                    min="0"
                    max="999999" v-model="formInline.dayProduction"></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="12">
                <el-form-item :label="CONSTANTS_DEVICE_CONFIGURATION.MANUFACTURER" prop="manufacturer">
                  <el-input v-model="formInline.manufacturer"></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="12">
                <el-form-item :label="CONSTANTS_DEVICE_CONFIGURATION.MANUFACTURER_TEL" prop="manufacturerTel">
                  <el-input v-model="formInline.manufacturerTel"></el-input>
                </el-form-item>
              </el-col>
            </el-col>
          </el-row>
        </el-form>
      </div>
    </el-main>
  </el-container>
</template>

<script>
import js from '@/assets/js/company/DeviceManange/deviceInfoBasics';
export default js;
</script>

<style lang="less" scoped>
@import '../../../assets/less/company/DeviceManange/deviceInfoBasics.less';
</style>
