<template>
  <el-container class="content">
    <el-header>
      <HeaderTitle class="header-title" :titleName="titleName">
        <div slot="head-search" class="head-search">{{ typeName }}</div>
      </HeaderTitle>
    </el-header>
    <el-main>
      <div class="area-main">
        <vxe-toolbar>
          <template v-slot:buttons>
            <vxe-input
              v-model="filterName"
              type="search"
              placeholder="全表搜索"
              @input="searchAllTable"
            ></vxe-input>
          </template>
        </vxe-toolbar>

        <vxe-grid
          border="full"
          resizable
          keep-source
          ref="xGrid"
          class="vxe-table-element"
          height="550"
          :checkbox-config="{ trigger: 'row', highlight: true, range: true, checkMethod: checCheckboxkAble }"
          :columns="tableColumns"
          :data="showList"
          @checkbox-all="selectAllEvent"
          @checkbox-change="selectChangeEvent"
        ></vxe-grid>
        <div class="btnDiv">
          <el-button type="primary" size="mini" @click="save">{{
            BTN_TEXT.SAVE
          }}</el-button>
          <el-button @click="goBack(0)" size="mini">{{ BTN_TEXT.RETURN }}</el-button>
        </div>
      </div>
    </el-main>
  </el-container>
</template>

<script>
import js from '@/assets/js/company/DeviceManange/addChildrenParams';
export default js;
</script>

<style lang="less" scoped>
@import '../../../assets/less/company/DeviceManange/addChildrenParams.less';
</style>
